<template>
    <div class="p-20">

        <div class="filter-container" style="margin-bottom:20px">
            <el-input class="filter-item" style="width: 300px;" placeholder="请输入用户ID、登录名"
                      @keyup.native.enter="conditionQuery"
                      v-model="currencyTrans.username">
            </el-input>
            <span>商品类型</span>
            <el-select clearable style="width: 150px" class="filter-item" v-model="currencyTrans.goodsId">
                <el-option v-for="status in coinList" :key="status.baseSeq" :label="status.chineseName"
                           :value="status.baseSeq">
                </el-option>
            </el-select>
            <el-button class="filter-item" @click="conditionQuery" size="small" style="margin-left: 30px">查询
            </el-button>
        </div>
        <div>
            <el-table
                    ref="singleTable"
                    :data="tableData"
                    v-loading="listLoading"
                    element-loading-text="给我一点时间">
                <el-table-column
                        v-for="item in tableHeader"
                        :key="item.prop"
                        :label="item.label"
                        :prop="item.prop"
                        :width="item.width"
                        :min-width="item.minWidth"
                        :align="item.align"
                        :formatter="item.formatter"
                        show-overflow-tooltip>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination @size-change="userPageSize" @current-change="userPage"
                               :current-page="currencyTrans.page.pageNum"
                               :page-sizes="[15,30,45,60]" :page-size="currencyTrans.page.pageSize"
                               layout="total, sizes, prev, pager, next, jumper" :total="total">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
    /*import fetch from '@/utils/fetch'*/
    import fetch from '@/utils/fetch';
    import DCTP from '@/utils/dctp';
    import {characterSize} from '@/utils/validate';
    import {goodsquery, getexpress, reviewgoods, lockgoods, cancelgoods, getgoodslist} from '@/api/awaits';
    //启用禁用筛选条件
    const allCoin = {baseSeq: '', chineseName: '全部商品', englishName: 'All'};
    const statuScope = [
        {key: 0, display_name: '全部状态'},
        {key: 2, display_name: '等待审核'},
        {key: 3, display_name: '成功提现'},
        {key: 4, display_name: '系统撤销'},
        {key: 5, display_name: '用户撤销'},
        {key: 6, display_name: '正在处理'},
    ];

    export default {
        data() {
            let validate = (rule, value, callback) => {
                if (value === '' || value === 0) {
                    callback(new Error('请选择快递'));
                }
                callback();
            };
            let validateNo = (rule, value, callback) => {
                if (characterSize(value) > 40) {
                    callback(new Error('最大40字符'));
                } else if (value === '') {
                    callback(new Error('快递单号不能为空'));
                }
                callback();
            };
            return {
                //搜索需求数据等
                allCoin,
                statuScope,
                coinList: [],
                goodsList: [],
                dateTime: '',
                tableData: [],
                deliverylist: [],
                goodsall: {
                    types: [2]
                },
                applygoods: {
                    id: '',
                    expressAgentId: 0,
                    expressNo: ''
                },
                formLabelWidth: '120px',
                total: null,
                dialogFormVisible: false,
                lockWithdrawalVo: {
                    id: '',
                    status: ''
                },

                temp: {
                    mobile: '',
                    location: '',
                    realName: '',
                    deliveryAddress: '',
                    applyDeliveryCount: '',
                    createTime: '',
                    goodsName: '',

                },
                currencyTrans: {
                    username: "",
                    goodsId: "",
                    status: 3,
                    page: {
                        pageSize: 15,
                        pageNum: 1
                    }

                },
                msg: '',
                listLoading: true,
                appgoods: {
                    expressAgentId: [{validator: validate, trigger: 'blur'}],
                    expressNo: [{validator: validateNo, trigger: 'blur'}],
                },
                tableHeader: [
                    {
                        label: "单号id",
                        prop: "id",
                        align: 'center',
                        width: 45
                    }, {
                        label: "用户UID",
                        prop: "userUid",
                        align: 'center',
                        width: 55
                    }, {
                        label: "登录名",
                        prop: "loginType",
                        width: 100,
                        align: 'center',
                        formatter(row, column, value) {
                            return value === 1 ? row.loginName : (value === 2 ? row.email : '未知');
                        }
                    }, {
                        label: "会员昵称",
                        prop: "nickName",
                        align: 'center',
                        width: 65
                    }, {
                        label: "真实姓名",
                        prop: "realName",
                        align: 'center',
                        width: 65
                    }, {
                        label: "商品类型",
                        prop: "goodsName",
                        align: 'center',
                        width: 65,

                    }, {
                        label: "状态",
                        prop: "status",
                        width: 65,
                        align: 'center',
                        formatter(row, column, value) {
                            let item = DCTP.utils.findDataByValue(statuScope, value, 'key');
                            return item ? item.display_name : '';
                        }
                    }, {
                        label: "数量",
                        prop: "applyDeliveryCount",
                        width: 50,
                        align: 'center',
                    }, {
                        label: "提货人",
                        prop: "realName",
                        align: 'center',
                        width: 65
                    }, {
                        label: "手机号",
                        prop: "mobile",
                        width: 100,
                        align: 'center'
                    }, {
                        label: "所在区域",
                        prop: "location",
                        width: 100
                    }, {
                        label: "收货地址",
                        prop: "deliveryAddress",
                        width: 320
                    }, {
                        label: "创建时间",
                        prop: "createTime",
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value)
                        }
                    }, {
                        label: "最后修改时间",
                        prop: "updateTime",
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value)
                        }
                    }
                ]
            }
        }
        ,
        computed: {
            tableDataLength() {
                return this.tableData.length;
            }
        }
        ,
        methods: {


            //获取当前页数
            userPage(val) {
                this.currencyTrans.page = val;
                this.getList();
            }
            ,
            //获取每页多少条
            userPageSize(val) {
                this.currencyTrans.pageSize = val;
                this.getList();
            }

            ,
            //条件查询方法
            conditionQuery() {
                this.getList();
            },
            getList() {
                this.listLoading = true;
                goodsquery(this.currencyTrans).then(response => {
                    this.tableData = response.data.data;
                    this.total = response.data.page.count;
                    if (!response.result) {
                        this.msg = response.msg;
                        this.$message({
                            type: 'warning',
                            message: this.msg
                        });
                    }
                    this.listLoading = false;
                })
            },
            getgoods() {
                getgoodslist(this.goodsall).then(response => {
                    this.coinList = response.data;
                    this.coinList.unshift(this.allCoin);
                })
            }
        },
        created() {
            this.getList();
            this.getgoods();
        }
    }
</script>



